*{
    margin: 0;
  }
  
  body {
    font-size: 24px;
  }
  
  h2 {
    font-size: 28px
  }
  
  #loop_machine {
    margin: 15px;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  
  .wrapper {
      width: 100%;
      display: grid;
      padding: 10px;
      grid-gap: 15px;
      margin-top: 40px;
      border-radius: 15px;
      grid-template-columns: 1fr;
      background-image: linear-gradient(
  -225deg
  , #ee9458 35%, #dfdc75 100%);
  }

  .wrapper-secondary {
    width: 53%;
    padding: 5px;
    grid-gap: 15px;
    margin-top: 50px;
    border-radius: 15px;
    grid-template-columns: 1fr;
    background-image: linear-gradient(
-225deg
, #eea97c 35%, #e1de84 100%);
}
  
  .keyboard {
      display: grid;
      grid-gap: 10px;
      grid-template-columns: repeat(3, 1fr);
  }
  
  .keyboard .instrument {
    width: 100%;
    height: 80px;
    outline: none;
    font-size: 24px;
    cursor: pointer;
    border-radius: 10px;
    background-color: #ffffff;
    transition: all .3s ease-in-out
  }
  
  @media(min-width: 768px){
    .wrapper {
        width: 680px;
    }
  }
  
  @media(min-width: 992px){
    .wrapper {
       grid-template-columns: 1fr 1fr;
    }
  }
  
  .control {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  
  .control > button,
  .control > input,
  .control > h2{
    margin-bottom: 10px
  }
  
  .control button{
    border: none;
    color: #ffffff;
    cursor: pointer;
    font-size: 20px;
    padding: 8px 10px;
    border-radius: 5px;
    background-color: #000000;
  }